import React from 'react'

const EditPatient = () => {
    return (
        <div className='edit'>
             <h1 className="edit-heading">Edit Patient</h1>
            <form className="edit-form">
                <input type="text" className="edit-input" placeholder='edit name' />
                <input type="text" className="edit-input" placeholder='edit category' />
                <input type="text" className="edit-input" placeholder='edit complain' />
                <input type="text" className="edit-input" placeholder='edit visit date' />
                <input type="text" className="edit-input" placeholder='edit medicine prescribed' />
                <button className="edit-btn">
                    Edit
                </button>
            </form>    
        </div>
    )
}

export default EditPatient
