import React from 'react'

const EditMedicine = () => {
    return (
        <div className='edit'>
            <h1 className="edit-heading">Edit medicine</h1>
            <form className="edit-form">
            <input type="text" className="edit-input" placeholder='edit name' />
                <input type="text" className="edit-input" placeholder='edit category' />
                <input type="text" className="edit-input" placeholder='edit price' />
                <input type="text" className="edit-input" placeholder='edit discount' />
                <input type="text" className="edit-input" placeholder='edit exp.date' />
                <input type="text" className="edit-input" placeholder='edit stock' />
                <button className="edit-btn">
                    Edit
                </button>
            </form>
        </div>
    )
}

export default EditMedicine
