import React, {useEffect} from 'react';
import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import {AiFillEdit, AiFillDelete} from "react-icons/ai";

import moment from 'moment';

import {fetchPatients} from '../Redux/ActionCreators/patientActions';

const PatientTable = ({comp, totalPatients, showPatientInputs}) => {

  const searchedArray = useSelector(store => store.searchedItems.patientSearchedItems);
  const {fetchedPatients} = useSelector(store => store.fetchedPatientsData)

  const patientArray = comp === 'search-results' ? searchedArray : comp === 'patient-list' ? fetchedPatients : [];

  const dispatch = useDispatch();

  useEffect(() => {
    if(comp === 'patient-list') {
        dispatch(fetchPatients());
    }
  }, [dispatch]); 

  return (
    <>
    {(patientArray && patientArray.length > 0) && 
    <div className="layout-container">
        <table className='layout-table'>
        <thead>
        <tr className='layout-tableheading'>
            <th className="layout-tableheader">Sl.No</th>
            <th className="layout-tableheader">Name</th>
            <th className="layout-tableheader">Category</th>
            <th className="layout-tableheader">Complain</th>
            <th className="layout-tableheader">Vist. Date</th>
            <th className="layout-tableheader">Med. Prescribed</th>
            {comp !== 'search-results' && 
            <th className="layout-tableheader">Edit</th>}
            {comp !== 'search-results' && 
            <th className="layout-tableheader">Delete</th>}
        </tr>
        </thead>
        <tbody className='layout-tablebody'>
            {patientArray.map((patient, index) => {
                const visitDate = moment(patient.visitDate).utc();
                const date = visitDate.date();
                const year = visitDate.year();
                const month = visitDate.month()+1;
                return (
                    <tr key={index}>
                    <td className="layout-tabledata">{index + 1}</td>
                    <td className="layout-tabledata">{patient.name}</td>
                    <td className="layout-tabledata">{patient.category}</td>
                    <td className="layout-tabledata">{patient.complain}</td>
                    <td className="layout-tabledata">{`${date}/${month}/${year}`}</td>
                    <td className="layout-tabledata">{patient.medPrescribed}</td>
                    <td className="layout-tabledata">
                    {comp !== 'search-results' && 
                    <Link style={{cursor: 'pointer'}} to={'/edit/patient'}>
                        <AiFillEdit />
                    </Link>}
                    </td>
                    {comp !== 'search-results' && 
                    <td style={{cursor: 'pointer'}} className="layout-tabledata">
                        <AiFillDelete onClick={()=> alert("deleted")} />
                    </td>}
                    </tr>
                )
            }) }
            
        </tbody>
        </table>
    </div>}

    {patientArray && patientArray.length === 0 && 
    <h2 style={{marginBottom: '1rem'}}>No results found.</h2>}
    </>
  )
}

export default PatientTable