import React, {useEffect} from 'react'
import { Link } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import {AiFillDelete, AiFillEdit} from "react-icons/ai";
import moment from 'moment';

import {fetchMedicines} from '../Redux/ActionCreators/medicineActions';

const MedicineTable = ({comp}) => {

    const searchedArray = useSelector(store => store.searchedItems.medicineSearchedItems);
    const {fetchedMedicines} = useSelector(store => store.fetchedMedicinesData);

    const medicineArray = comp === 'search-results' ? searchedArray : comp === 'medicine-list' ? fetchedMedicines : []; 

    const dispatch = useDispatch();

    useEffect(() => {
        if(comp === 'medicine-list') {
            dispatch(fetchMedicines());
        }
    }, []);

    return (
        <>
        {medicineArray && medicineArray.length > 0 && 
        <div className="layout-container">
            <table className='layout-table'>
            <thead>
            <tr className='layout-tableheading'>
            <th className="layout-tableheader">Sl.No</th>
            <th className="layout-tableheader">Name</th>
            <th className="layout-tableheader">Category</th>
            <th className="layout-tableheader">Price</th>
            <th className="layout-tableheader">Discount</th>
            <th className="layout-tableheader">Exp. Date</th>
            <th className="layout-tableheader">Stock</th>
            {comp !== 'search-results' && 
            <th className="layout-tableheader">Edit</th>}
            {comp !== 'search-results' && 
            <th className="layout-tableheader">Delete</th>}
            </tr>
            </thead>
            <tbody className='layout-tablebody'>
            {medicineArray && medicineArray.map((medicine, index) => {
                const expDate = moment(medicine.expDate).utc();
                const date = expDate.date();
                const year = expDate.year();
                const month = expDate.month()+1;
                return (
                    <tr key={index}>
                        <td className="layout-tabledata">{index + 1}</td>
                        <td className="layout-tabledata">{medicine.name}</td>
                        <td className="layout-tabledata">{medicine.category}</td>    
                        <td className="layout-tabledata">{medicine.price}</td>
                        <td className="layout-tabledata">{`${medicine.discount}%`}</td>
                        <td className="layout-tabledata">{`${date}/${month}/${year}`}</td>
                        <td className="layout-tabledata">{medicine.stock}</td>
                        {comp !== 'search-results' && <td className="layout-tabledata"> 
                        <Link to={"/edit/medicine"}>
                            <AiFillEdit />
                        </Link>  
                        </td>}
                        {comp !== 'search-results' && 
                        <td style={{cursor: 'pointer'}} className="layout-tabledata">
                            <AiFillDelete onClick={()=> alert("hello")} />
                        </td>}
                    </tr>
                )
            }) } 
            </tbody>
            </table>
        </div>}

        {medicineArray && medicineArray.length === 0 && 
        <h2 style={{marginBottom: '1rem'}}>
            No results found.
        </h2>}
        </> 
    )
}

export default MedicineTable
