import React from 'react';
import { useSelector } from 'react-redux';

import PatientTable from './PatientTable';
import MedicineTable from './MedicineTable';

const SearchResults = () => {
  const {searched} = useSelector(store => store.searchedItems);
  const {patientSearchedItems, medicineSearchedItems} = useSelector(store => store.searchedItems);

  return (
    <>
    {searched && 
    <div className='layout-search-results'>
        <h3 className='layout-search-results-heading'>Search Results: </h3>
        {searched === 'patients' &&
        <PatientTable comp='search-results' />}
        {searched === 'medicines' && 
        <MedicineTable comp='search-results' /> }
    </div>}
    </>
  )
}

export default SearchResults